.search-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 20px;
  line-height: 1.6;
}

h1 {
  text-align: center;
  background-color: beige;
  color: black;
  padding: 8m;
  border-radius: 20px;
}

img {
  display: block;
  margin: auto;
  border-radius: 20px;
}

h2 {
  margin: 20px 0;
  background-color: antiquewhite;
  border-radius: 10px;
  text-align: center;
}

video {
  border-radius: 10px;
}

table {
  width: 65%;
  border-collapse: collapse;
  margin: 5px;
}

.right-column {
  flex: 1;
  padding: 20px;
  background-color: #ddd;
  border-left: 12px #ccc;
  border-radius: 10px;
}

.main-content {
  flex: 3;
  padding: 20px;
  background-color: #f4f4f4;
  border-radius: 15px;
}

.page-container {
  display: flex;
}

.search-container {
  position: relative;
  width: 100px;
  margin: 2px;
}

.search-input {
  width: 100%;
  padding: 15px 30px;
  border: 2px #ccc;
  border-radius: 30px;
  font-size: 12px;
  font: bold;
  outline: none;
  transition: border-color 0.3s ease-in-out;
}

.search-input:hover {
  border-color: #888;
}

button {
  background-color: beige;
  color: #888;
  border: none;
  padding: 5px;
  border-radius: 5px;
  width: 100px;
  margin-left: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
  cursor: pointer;
  font-style: bold;
  font-family: "Times New Roman", Times, serif;
  position: relative;
}

button:hover {
  color: tomato;
}

button:active {
  color: blue;
}